<template>         
    <div>
        <div class="row">
            <div class="col-12 p-3">
                <div class="content-wrapper">
                    <p class="section-title clearfix mb-4">
                        Add Parameter 
                        <a class="btn btn-primary btn-sm float-right" :href="URL.base + '/parameter'"><i class="fa fa-arrow-left mr-2"></i>Back</a>
                    </p>
                    <form :action="URL.base + '/create-parameter'" method="POST"> 
                        <input type="hidden" name="_token" id="_token" :value="token">
                        <div class="row">
                            <div class="offset-md-2 col-md-8"> 
                                <div :class="['alert', 'alert-dismissible', message.status == 'success' ? 'alert-success' : 'alert-danger']" v-if="message">
                                    <button type="button" class="close" data-dismiss="alert">&times;</button>
                                    <div v-for="msg in message.body">
                                        {{msg}}
                                    </div>
                                </div> 
                                <div class="row">  
                                    <div class="form-group col-md-6">
                                        <label for="name"><strong>Name</strong></label>
                                        <input v-model="parameterInfo.name" type="text" class="form-control" id="name" name="name" placeholder="Name..." required="required">
                                    </div>        
                                    <div class="form-group col-md-6">
                                        <label for="connection_interval"><strong>Connection Interval</strong></label>
                                        <input v-model="parameterInfo.connection_interval" type="number" min="5" class="form-control" id="connection_interval" name="connection_interval" placeholder="Connection Interval..." required="required">
                                    </div>  
                                    <div class="form-group col-md-6">
                                        <label for="vol_min"><strong>Volume</strong></label>
                                        <input v-model="parameterInfo.vol_min" type="number" min="1" class="form-control" id="vol_min" name="vol_min" placeholder="Volume..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="vol_bin_size"><strong>Vol Bin Size</strong></label>
                                        <select v-model="parameterInfo.vol_bin_size" class="form-control" name="vol_bin_size" id="vol_bin_size" required="required">
                                            <option value="1m">1m</option>
                                            <option value="5m">5m</option>
                                            <option value="1h">1h</option>
                                            <option value="1d">1d</option>
                                        </select>
                                    </div>                
                                    <div class="form-group col-md-6">
                                        <label for="vol_count"><strong>Volume Count</strong></label>
                                        <input v-model="parameterInfo.vol_count" type="number" min="1" max="1000" class="form-control" id="vol_count" name="vol_count" placeholder="Volume Count..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="limit_profit"><strong>Limit Profit</strong></label>
                                        <input v-model="parameterInfo.limit_profit" type="number" min="1" class="form-control" id="limit_profit" name="limit_profit" placeholder="Limit Profit..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="order_count"><strong>Order Count</strong></label>
                                        <input v-model="parameterInfo.order_count" type="number" min="0" class="form-control" id="order_count" name="order_count" placeholder="Order Count..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="order_distance"><strong>Order Distance</strong></label>
                                        <input v-model="parameterInfo.order_distance" type="number" min="1" class="form-control" id="order_distance" name="order_distance" placeholder="Order Distance..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="qty_rate"><strong>QTY Rate</strong></label>
                                        <input v-model="parameterInfo.qty_rate" type="number" min="0.0001" max="1" step="0.0001" class="form-control" id="qty_rate" name="qty_rate" placeholder="QTY Rate..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="stop_loss"><strong>Stop Loss</strong></label>
                                        <input v-model="parameterInfo.stop_loss" type="number" min="0" class="form-control" id="stop_loss" name="stop_loss" placeholder="Stop Loss..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="force_stop"><strong>Force Stop</strong></label>
                                        <select v-model="parameterInfo.force_stop" class="form-control" name="force_stop" id="force_stop" required="required">
                                            <option value="0">Disabled</option>
                                            <option value="1">Enabled</option>
                                        </select>
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="martingale"><strong>Martingale</strong></label>
                                        <select v-model="parameterInfo.martingale" class="form-control" name="martingale" id="martingale" required="required">
                                            <option value="1">None</option>
                                            <option value="1.5">1.5x</option>
                                            <option value="2">2x</option>
                                        </select>
                                    </div> 
                                    <div class="form-group col-md-6">
                                        <label for="inverse"><strong>Inverse</strong></label>
                                        <select v-model="parameterInfo.inverse" class="form-control" name="inverse" id="inverse" required="required">
                                            <option value="0">False</option>
                                            <option value="1">True</option>
                                        </select>
                                    </div> 
                                    <div class="form-group col-md-6">
                                        <label for="direction"><strong>Buy / Sell</strong></label>
                                        <select v-model="parameterInfo.direction" class="form-control" name="direction" id="direction" required="required">
                                            <option value="0">Both</option>
                                            <option value="1">Buy Only</option>
                                            <option value="2">Sell Only</option>
                                        </select>
                                    </div> 
                                </div>
                                <button type="submit" class="btn btn-primary float-right btn-md mt-3">Create Parameter</button>                        
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {

        props: ['message', 'oldInput'],

        data() {
            return {
                URL: window.URL,
                token: window.token,   
                parameterInfo: {               
                    name: this.oldInput && this.oldInput.name ? this.oldInput.name : null, 
                    connection_interval: this.oldInput && this.oldInput.connection_interval ? this.oldInput.connection_interval : null,
                    vol_min: this.oldInput && this.oldInput.vol_min ? this.oldInput.vol_min : null,
                    vol_bin_size: this.oldInput && this.oldInput.vol_bin_size ? this.oldInput.vol_bin_size : '1m',
                    vol_count: this.oldInput && this.oldInput.vol_count ? this.oldInput.vol_count : null,
                    limit_profit: this.oldInput && this.oldInput.limit_profit ? this.oldInput.limit_profit : null,
                    order_count: this.oldInput && this.oldInput.order_count ? this.oldInput.order_count : null,
                    order_distance: this.oldInput && this.oldInput.order_distance ? this.oldInput.order_distance : null,
                    qty_rate: this.oldInput && this.oldInput.qty_rate ? this.oldInput.qty_rate : null,
                    stop_loss: this.oldInput && this.oldInput.stop_loss ? this.oldInput.stop_loss : null,
                    force_stop: this.oldInput && this.oldInput.force_stop ? this.oldInput.force_stop : 1,
                    martingale: this.oldInput && this.oldInput.martingale ? this.oldInput.martingale : 1,
                    inverse: this.oldInput && this.oldInput.inverse ? this.oldInput.inverse : 0,
                    direction: this.oldInput && this.oldInput.direction ? this.oldInput.direction : 0,
                }
            }
        },
        computed: {
        },
        methods:{
        },
        mounted: function(){
        }
    }
    
</script>
