<template>         
    <div>
        <div class="row">        
            <div class="col-12 p-3">
                <div class="content-wrapper">
                    <div class="section-title clearfix mb-4 d-flex flex-wrap align-items-center">             
                        <div class="d-flex flex-grow-1 text-nowrap align-items-center">
                            <div>
                                <input type="text" class="form-control search" placeholder="search..." @change="search">
                            </div>
                            <div class="flex-grow-1"></div>
                            <div>
                                <a class="btn btn-primary float-right d-inline-block" :href="URL.base + '/add-parameter'">Add Parameter +</a>
                            </div>
                        </div>
                    </div>
                    <div :class="['table-container','data-loading',loading&&'loading']">
                        <div>
                            <table class="table table-hover">
                                <thead>
                                    <tr>
                                        <th>No</th>
                                        <th :class="['th-order',orderBy==='name'&&(orderByDesc?'desc':'asc')]" @click="sort('name')">Name</th>
                                        <th :class="['th-order',orderBy==='connection_interval'&&(orderByDesc?'desc':'asc')]" @click="sort('connection_interval')">Connection Interval</th>
                                        <th :class="['th-order',orderBy==='vol_min'&&(orderByDesc?'desc':'asc')]" @click="sort('name')">Volume</th>
                                        <th :class="['th-order',orderBy==='vol_bin_size'&&(orderByDesc?'desc':'asc')]" @click="sort('vol_bin_size')">Vol Min Size</th>
                                        <th :class="['th-order',orderBy==='vol_count'&&(orderByDesc?'desc':'asc')]" @click="sort('vol_count')">Volume Count</th>
                                        <th :class="['th-order',orderBy==='limit_profit'&&(orderByDesc?'desc':'asc')]" @click="sort('limit_profit')">Limit Profit</th>
                                        <th :class="['th-order',orderBy==='order_count'&&(orderByDesc?'desc':'asc')]" @click="sort('order_count')">Order Count</th>
                                        <th :class="['th-order',orderBy==='order_distance'&&(orderByDesc?'desc':'asc')]" @click="sort('order_distance')">Order Distance</th>
                                        <th :class="['th-order',orderBy==='qty_rate'&&(orderByDesc?'desc':'asc')]" @click="sort('qty_rate')">QTY Rate</th>
                                        <th :class="['th-order',orderBy==='stop_loss'&&(orderByDesc?'desc':'asc')]" @click="sort('stop_loss')">Stop Loss</th>
                                        <th :class="['th-order',orderBy==='force_stop'&&(orderByDesc?'desc':'asc')]" @click="sort('force_stop')">Force Stop</th>
                                        <th :class="['th-order',orderBy==='martingale'&&(orderByDesc?'desc':'asc')]" @click="sort('martingale')">Martingale</th>
                                        <th :class="['th-order',orderBy==='inverse'&&(orderByDesc?'desc':'asc')]" @click="sort('inverse')">Inverse</th>
                                        <th :class="['th-order',orderBy==='direction'&&(orderByDesc?'desc':'asc')]" @click="sort('direction')">Buy/Sell</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(parameter, index) in list">
                                        <td>{{ index+1 }}</td>
                                        <td>{{ parameter.name }}</td>
                                        <td>{{ parameter.connection_interval }}</td>
                                        <td>{{ parameter.vol_min }}</td>
                                        <td>{{ parameter.vol_bin_size }}</td>
                                        <td>{{ parameter.vol_count }}</td>
                                        <td>{{ parameter.limit_profit }}</td>
                                        <td>{{ parameter.order_count }}</td>     
                                        <td>{{ parameter.order_distance }}</td>      
                                        <td>{{ parameter.qty_rate }}</td>    
                                        <td>{{ parameter.stop_loss }}</td>    
                                        <td>{{ parameter.force_stop == 1 ? 'Enabled' : 'Disabled' }}</td>    
                                        <td>{{ parameter.martingale == 1 ? 'None' : (parameter.martingale == 1.5 ? '1.5x' : '2x') }}</td>
                                        <td>{{ parameter.inverse == 1 ? 'True' : 'False' }}</td>    
                                        <td>{{ parameter.direction == 0 ? 'Both' : parameter.direction == 1 ? 'Buy Only' : 'Sell Only' }}</td>            
                                        <td class="text-right">                                    
                                            <a class="btn btn-primary btn-sm" :href="URL.base + '/edit-parameter/' + parameter.id"><i class="fa fa-edit"></i></a>
                                            <a class="btn btn-primary btn-sm"  data-toggle="modal" data-target="#confirmModal"  @click.prevent="parameterDelete(parameter.id)"><i class="fa fa-trash"></i></a>
                                        </td>
                                    </tr>
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td colspan="16" :class="[!loading&&msg&&'footer-bottom']">
                                            <p v-if="!loading&&msg">{{msg}}</p>
                                        </td>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                    </div>
                    <div class="div-pagination">
                        <ul class="pagination float-right">
                            <li v-if="pageNow>1" :class="['page-item',pageNow<=1&&'disabled']">
                                <a class="page-link" href="#" @click.prevent="goPage(1)">First</a>
                            </li>
                            <li v-if="pageNow>1" :class="['page-item',pageNow<=1&&'disabled']">
                                <a class="page-link" href="#" @click.prevent="goPage(pageNow-1)">Prev</a>
                            </li>
                            <li v-for="page in pageArray"  :class="['page-item',pageNow===page&&'active']">
                                <a class="page-link" href="#" @click.prevent="goPage(page)">{{page}}</a>
                            </li>
                            <li v-if="pageNow<pageCount" :class="['page-item',pageNow>=pageCount&&'disabled']">
                                <a class="page-link" href="#" @click.prevent="goPage(pageNow+1)">Next</a>
                            </li>
                            <li v-if="pageNow<pageCount" :class="['page-item',pageNow>=pageCount&&'disabled']">
                                <a class="page-link" href="#" @click.prevent="goPage(pageCount)">Last</a>
                            </li>
                        </ul>
                        <p v-if="count>0">
                            Showing {{offset+1}} to {{offset+countShow}} of {{count}} parameters
                        </p>
                    </div>
                </div>
            </div>
        </div>

        <!-- The Modal -->
        <div class="modal fade" id="confirmModal">
            <div class="modal-dialog">
                <div class="modal-content">
                  
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">Delete Parameter</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>
                    
                    <!-- Modal body -->
                    <div class="modal-body">
                        Are you sure to delete this parameter? This operation is irreversible.
                    </div>
                    
                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <a :href="URL.base + '/delete-parameter/' + deleteId" class="btn btn-primary">Confirm</a>
                        <button type="button" class="btn btn-danger" data-dismiss="modal">Cancel</button>  
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {

        props: [],

        data() {
            return {
                URL: window.URL,
                PAGEBAR: 10,
                loading: true,
                count: 0,
                offset: 0,
                limit: 100,
                orderBy: 'id',
                orderByDesc: false,
                key: '',
                msg: false,
                list: [],
                deleteId: null
            }
        },
        computed: {
            countShow: function(){
                return this.list.length;
            },
            pageNow: function(){
                return Math.floor(this.offset / this.limit) + 1;
            },
            pageCount: function(){
                return Math.floor((this.count - 1) / this.limit) + 1;
            },
            pageFirst: function(){
                return Math.floor((this.pageNow - 1) / this.PAGEBAR) * this.PAGEBAR+1;
            },
            pageLast: function(){
                var pageLast=this.pageFirst+this.PAGEBAR-1;
                if(pageLast>this.pageCount)return this.pageCount;
                return pageLast;
            },
            pageArray: function(){
                var array=[];
                for(var i=this.pageFirst;i<=this.pageLast;i++)
                    array.push(i);
                return array;
            }
        },
        methods:{
            goPage: function(target){
                var limit=this.limit;
                var offset = (target - 1) * limit;
                if(offset<0)offset=0;
                else if(offset>this.count)offset=Math.floor((this.count - 1) / limit) * limit;
                this.offset = offset;
                this.getList();
            },
            sort: function(by){
                if(by===this.orderBy){
                    this.orderByDesc=!this.orderByDesc;
                }else{
                    this.orderBy=by;
                    this.orderByDesc=false;
                }
                this.getList();
            },
            getList: function(){
                var vm=this;
                vm.loading = true;
                var param = {
                    page: this.pageNow,
                    offset: this.offset,
                    limit: this.limit,
                    orderby: this.orderBy,
                    orderByDesc: this.orderByDesc?1:0,
                    key: this.key
                }

                axios.post(this.URL.base + '/get-parameters', param, {})
                .then(response => {
                    var responseData = response.data;
                    vm.count=responseData.count;
                    vm.offset=responseData.offset;
                    vm.limit=responseData.limit;
                    vm.orderBy=responseData.orderBy;
                    vm.orderByDesc=responseData.orderByDesc;
                    vm.list=responseData.result;
                    this.msg=vm.countShow<1?'No data found.':false;
                    vm.loading = false;
                })
                .catch(error => {
                    console.log(error)
                    this.msg="Server connection failed.";
                })
                .finally(() => this.loading = false)
            },
            
            search(e) {
                this.key = e.target.value;
                this.getList();
            },

            parameterDelete(id)
            {
                this.deleteId = id;
            },
        },
        mounted: function(){
            this.getList();
        }
    }
    
</script>
