<template>         
    <div>
        <div class="row">
            <div class="col-12 p-3">
                <div class="content-wrapper">
                    <p class="section-title clearfix mb-4">
                        Add User 
                        <a class="btn btn-primary btn-sm float-right" :href="URL.base + '/home'"><i class="fa fa-arrow-left mr-2"></i>Back</a>
                    </p>
                    <form :action="URL.base + '/create-client'" method="POST"> 
                        <input type="hidden" name="_token" id="_token" :value="token">
                        <div class="row">
                            <div class="offset-md-2 col-md-8"> 
                                <div :class="['alert', 'alert-dismissible', message.status == 'success' ? 'alert-success' : 'alert-danger']" v-if="message">
                                    <button type="button" class="close" data-dismiss="alert">&times;</button>
                                    <div v-for="msg in message.body">
                                        {{msg}}
                                    </div>
                                </div> 
                                <div class="row">  
                                    <div class="form-group col-md-6">
                                        <label for="api"><strong>API</strong></label>
                                        <input v-model="userInfo.api" type="text" class="form-control" id="api" name="api" placeholder="API..." required="required">
                                    </div> 
                                    <div class="form-group col-md-6">
                                        <label for="secret_key"><strong>Secret Key</strong></label>
                                        <input v-model="userInfo.secret_key" type="text" class="form-control" id="secret_key" name="secret_key" placeholder="Secret Key..." required="required">
                                    </div>                              
                                    <div class="form-group col-md-6">
                                        <label for="name"><strong>Name</strong></label>
                                        <input v-model="userInfo.name" type="text" class="form-control" id="name" name="name" placeholder="Name..." required="required">
                                    </div>        
                                    <div class="form-group col-md-6">
                                        <label for="email"><strong>Email</strong></label>
                                        <input v-model="userInfo.email" type="email" class="form-control" id="email" name="email" placeholder="E-mail..." required="required">
                                    </div>  
                                    <div class="form-group col-md-6">
                                        <label for="license"><strong>License</strong></label>
                                        <input v-model="userInfo.license" type="text" class="form-control" id="license" name="license" placeholder="License..." required="required">
                                    </div>  
                                    <div class="form-group col-md-6">
                                        <label for="expire_date"><strong>Expire Date</strong></label>
                                        <date-picker v-model="userInfo.expire_date" :config="options" id="expire_date" name="expire_date"></date-picker>
                                    </div>   
                                    <div class="form-group col-md-6">
                                        <label for="parameter"><strong>Parameter</strong></label>
                                        <select v-model="userInfo.parameter" class="form-control" name="parameter" id="parameter" required="required">
                                            <option :value="parameter.id" v-for="parameter in parameterList">{{ parameter.name }}</option>
                                        </select>
                                    </div>                                                
                                    <div class="form-group col-md-6">
                                        <label for="balance_max"><strong>Max Balance</strong></label>
                                        <select v-model="userInfo.balance_max" class="form-control" name="balance_max" id="balance_max" required="required">
                                            <option value="1000000">0.01 XBT</option>
                                            <option value="10000000">0.1 XBT</option>
                                            <option value="50000000">0.5 XBT</option>
                                            <option value="100000000">1 XBT</option>
                                            <option value="500000000">5 XBT</option>
                                            <option value="1000000000">10 XBT</option>
                                            <option value="2000000000">20 XBT</option>
                                            <option value="3000000000">30 XBT</option>
                                            <option value="4000000000">40 XBT</option>
                                            <option value="5000000000">50 XBT</option>
                                            <option value="6000000000">60 XBT</option>
                                            <option value="7000000000">70 XBT</option>
                                            <option value="8000000000">80 XBT</option>
                                            <option value="9000000000">90 XBT</option>
                                            <option value="10000000000">100 XBT</option>
                                        </select>
                                    </div>   
                                    <div class="form-group col-md-6">
                                        <label for="status"><strong>Status</strong></label>
                                        <select v-model="userInfo.status" class="form-control" name="status" id="status" required="required">
                                            <option value="1">Active</option>
                                            <option value="0">Paused</option>
                                        </select>
                                    </div>  
                                    <div class="form-group col-md-6">
                                        <label for="remark"><strong>Remark</strong></label>
                                        <input v-model="userInfo.remark" type="text" class="form-control" id="remark" name="remark" placeholder="Remark...">
                                    </div>
                                </div>
                                <button type="submit" class="btn btn-primary float-right btn-md mt-3">Create User</button>                        
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {

        props: ['message', 'oldInput', 'parameterList'],

        data() {
            return {
                URL: window.URL,
                token: window.token,
                options: {
                  format: 'MM/DD/YYYY',
                  useCurrent: false,
                },     
                userInfo: {               
                    name: this.oldInput && this.oldInput.name ? this.oldInput.name : null, 
                    email: this.oldInput && this.oldInput.email ? this.oldInput.email : null,
                    api: this.oldInput && this.oldInput.api ? this.oldInput.api : null,
                    secret_key: this.oldInput && this.oldInput.secret_key ? this.oldInput.secret_key : null,
                    license: this.oldInput && this.oldInput.license ? this.oldInput.license : null,
                    expire_date: this.oldInput && this.oldInput.expire_date ? this.oldInput.expire_date : new Date(),
                    parameter: this.oldInput && this.oldInput.parameter ? this.oldInput.parameter : 1,
                    balance_max: this.oldInput && this.oldInput.balance_max ? this.oldInput.balance_max : 1000000,
                    status: this.oldInput && this.oldInput.status ? this.oldInput.status : 1,
                    remark: this.oldInput && this.oldInput.remark ? this.oldInput.remark : null
                }
            }
        },
        computed: {
        },
        methods:{
        },
        mounted: function(){
        }
    }
    
</script>
